/* ===== Header base ===== */
.s-header {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;
  width: 100%;
  height: 88px;
  background-color: #ffffff;
  box-shadow: 0 4px 10px rgba(0,0,0,0.08);
  display: flex;
  align-items: center;
}

/* ===== Logo ===== */
.s-header__logo {
  position: absolute;
  left: 80px;
  top: 50%;
  transform: translateY(-50%);
}

.logo-wrapper {
  display: block;
  position: relative;
  width: 200px;
  height: 100px;
}

.logo-completo,
.logo-isotipo {
  position: absolute;
  inset: 0;
  margin: auto;
  height: 100%;
  width: auto;
  max-width: 100%;
  object-fit: contain;
  opacity: 1;
  transform: scale(1);
  transition: opacity 0.6s ease, transform 0.6s ease;
}

/* Isotipo oculto al inicio (por si luego querés animar scroll) */
.logo-isotipo {
  opacity: 0;
  transform: scale(0.9);
}

/* Si agregás la clase .scrolled al header, invierte logo/isotipo */
.s-header.scrolled .logo-completo {
  opacity: 0;
  transform: scale(1.05);
}
.s-header.scrolled .logo-isotipo {
  opacity: 1;
  transform: scale(1);
}

/* ===== Contenido (nav + banderas) ===== */
.s-header__content {
  position: relative;
  height: 100%;
  display: flex;
  align-items: center;
  gap: 32px;
  width: 100%;
  padding-left: 316px; /* deja espacio para el logo */
  padding-right: 84px;
  box-sizing: border-box;
}

/* ===== Nav ===== */
.s-header__nav-wrap {
  display: flex;
  align-items: center;
}

.s-header__nav {
  list-style: none;
  display: flex;
  gap: 16px;
  margin: 0;
  padding: 0;
}

.s-header__nav li {
  margin: 0;
}

.s-header__nav a {
  display: inline-block;
  padding: 8px 10px;
  color: #000000;
  text-decoration: none;
  font-weight: 600;
  letter-spacing: 0.08em;
  text-transform: uppercase;
  font-size: 14px;
  line-height: 1;
  transition: color 0.2s ease;
}

.s-header__nav a:hover,
.s-header__nav a:focus {
  color: #F88379; /* coral */
}

/* ===== Selector de idioma ===== */
.radio-inputLoco {
  display: flex;
  align-items: center;
  gap: 12px;
  margin-left: auto; /* empuja a la derecha */
}

.divInputLoco {
  display: flex;
  align-items: center;
  gap: 6px;
}

.inputLoco {
  width: 16px;
  height: 16px;
  accent-color: #F88379;
  cursor: pointer;
}

.langFlag {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  display: block;
}

/* ===== Toggle móvil (oculto en desktop) ===== */
.s-header__menu-toggle {
  display: none;
  position: absolute;
  right: 52px;
  top: 16px;
  width: 42px;
  height: 42px;
  background: transparent;
  border: 1px solid #F88379;
  color: #F88379;
  border-radius: 6px;
  cursor: pointer;
}

/* ===== Responsivo ===== */
@media (max-width: 1000px) {
  .s-header__logo { left: 60px; }
  .s-header__content { padding-left: 296px; }
}

@media (max-width: 900px) {
  .s-header { height: 80px; }

  .s-header__logo {
    left: 40px;
  }
  .logo-wrapper {
    width: 160px;
    height: 80px;
  }

  .s-header__content {
    padding-left: 220px;
    padding-right: 64px;
  }

  .s-header__nav a {
    font-size: 13px;
    letter-spacing: 0.06em;
    padding: 8px 8px;
  }
}

@media (max-width: 768px) {
  .s-header__menu-toggle { display: block; }

  .s-header__content {
    position: absolute;
    left: 0;
    top: 80px; /* debajo del header en mobile */
    width: 100%;
    background-color: #ffffff;
    padding: 20px 24px;
    display: none; /* por defecto oculto, mostrás con una clase desde JS si querés */
    box-shadow: 0 8px 16px rgba(0,0,0,0.08);
  }

  .s-header__content.is-open {
    display: block;
  }

  .s-header__nav {
    flex-direction: column;
    align-items: flex-start;
    gap: 10px;
    padding: 0;
    margin-bottom: 16px;
  }

  .radio-inputLoco {
    margin-left: 0;
  }
}

@media (max-width: 600px) {
  .s-header__logo { left: 32px; }
  .logo-wrapper { width: 130px; height: 70px; }
}

@media (max-width: 400px) {
  .s-header__logo { left: 24px; }
}
